import { isNodesEquivalent } from '@babel/types';
import React, { useEffect, Component } from 'react';
import Web3 from 'web3';
import ABI from '../data/vip.json'
import LP_ABI from '../data/LP_ABI.json'
const VIP_ADDRESS = '0x2F977E9eAb5A854d2bAA853502a3e85320B258E8'
const LP_ADDRESS = '0xbf0b068b00BBeddFD37310A1fbb4035130453301'

const VipInfo = () => {
    return (
        <div>
            <section id="vip-benefit" className=" pt-5  justify-content-between">
                <h4>VIP benefits</h4>
                <ul>
                    <li>Can use get LP price command with <a href="https://t.me/waterfall_help_bot" rel="noopener noreferrer" target="_blank"><b>@waterfall_help_bot</b></a><i> (example /lp_WBNBBUSD)</i></li>
                </ul>
            </section>

            <section id="vip-fee" className=" pt-5  justify-content-between">
                <h4>VIP fee</h4>
                <ul>
                    <li>No charged fee</li>
                    <li>Just provide liquidity for WBNB-WBT on Pancakeswap and hold LP token</li>
                    <li>If you don't like VIP, remove liquidity and get your money back anytime</li>
                </ul>
            </section>

            <section id="become-vip" className=" pt-5  justify-content-between">
                <h4>How to become VIP (Hold WBNB-WBT LP in your wallet)</h4>
                <ul>
                    <li><a className="text-underline" href="https://exchange.pancakeswap.finance/#/swap?outputCurrency=0xc7435088819F1AF950357fED65BE856165cC3abB" rel="noopener noreferrer" target="_blank">BUY WBT</a> on Pancakeswap</li>
                    <li>Add liquidity for <a href="https://exchange.pancakeswap.finance/#/add/BNB/0xc7435088819F1AF950357fED65BE856165cC3abB" className="text-underline" rel="noopener noreferrer" target="_blank">WBNB - WBT</a></li>
                </ul>
            </section>
        </div>
    );
}
let web3, web3Instance, vipContract
const isMetaMaskInstalled = () => {
    //Have to check the ethereum binding on the window object to see if it's installed
    const { ethereum, web3 } = window;
    return Boolean((ethereum && ethereum.isMetaMask) || (web3));
};
const handleNoMetamask = () => {
    alert('Please install Metamask or use Trust wallet');
}

const checkBSC = () => {
    if (!web3) {
        web3 = window.ethereum ? window.ethereum : window.web3
    }
    let id = web3.chainId
    return (id === "0x38" || id === '56' || id == 56)
}

const MetamaskClientCheck = async () => {
    if (!web3) {
        web3 = window.ethereum ? window.ethereum : window.web3
    }

    let accs = await web3.request({ method: 'eth_requestAccounts' })
    let isBSC = checkBSC()
    if (!isBSC) {
        alert('Please switch to Binance Smart Chain')
        return null
    }

    return accs[0]
}

class VIP extends Component {
    state = { wallet: "", balance: 0, vip: false, telegram: '', newTelegram : '', minVip: '' };
    async componentDidMount() {
        if (!isMetaMaskInstalled()) {
            return handleNoMetamask()
        } else {
            let w = await MetamaskClientCheck()
            if (!w) {
                return
            }
            web3Instance = new Web3(web3)
            vipContract = new web3Instance.eth.Contract(ABI, VIP_ADDRESS)
            let isVip = await vipContract.methods.isVip(w).call()
            let balance = await vipContract.methods.getBalance(w).call()
            balance = parseInt(balance.toString() / 10 ** 18)
            let telegram = await vipContract.methods.getTelegramID(w).call()
            let minLP = await vipContract.methods.minAmount().call()
            // check LP
            
            let lpContract = new web3Instance.eth.Contract(LP_ABI, LP_ADDRESS)
            let reserved = await lpContract.methods.getReserves().call()
            let totalLP = await lpContract.methods.totalSupply().call()
            let rate = minLP / totalLP 
            let minWBNB = parseFloat(rate * reserved[0] / 10 ** 18).toFixed(4)
            let minWBT = parseFloat(rate * reserved[1] / 10 ** 18).toFixed(4)
            this.setState({
                wallet: w,
                balance: balance,
                vip: isVip,
                telegram: telegram,
                minVip: `${parseInt(minLP.toString() / 10 ** 18)} LP WBNB-WBT (${minWBNB} BNB + ${minWBT} WBT)`
            })
        }

    }
    async onConnect () {
        this.componentDidMount()
    }
    async updateTelegram () {
        let oldID = this.state.telegram
        let newID = this.state.newTelegram
        let w = this.state.wallet
        await vipContract.methods.setVipData(oldID, newID).send({
            from: w
        })
    }
    updateInputValue(evt) {
        this.setState({
          newTelegram: evt.target.value
        });
      }
    render() {
        if (this.state.wallet && this.state.wallet !== "") {
            if (!this.state.vip) {
                return (
                    <>
                        <div className="pt-5  justify-content-between">
                            <div>
                                <span>Your wallet </span>
                                <span>{this.state.wallet.substr(0,5) + "..." + this.state.wallet.substr(this.state.wallet.length - 3)}</span>
                            </div>
                            <div>
                                <span>Balance </span>
                                <span>{this.state.balance} LP WBNB-WBT</span>
                            </div>
                            <div className="pt-5  justify-content-between">
                            Please <a className="text-underline" href="https://exchange.pancakeswap.finance/#/swap?outputCurrency=0xc7435088819F1AF950357fED65BE856165cC3abB" rel="noopener noreferrer" target="_blank">BUY WBT</a> and <a className="text-underline" href="https://exchange.pancakeswap.finance/#/add/BNB/0xc7435088819F1AF950357fED65BE856165cC3abB" rel="noopener noreferrer" target="_blank">add liquidity WBNB-WBT</a> to be <b className="text-danger">VIP</b>
                            <p>Required: <i className="text-danger">{this.state.minVip}</i></p>
                            </div>
                        </div>
                        <VipInfo />
                    </>
                )
            } else {
                return (
                    <>
                        <div className="pt-5  justify-content-between">
                            <div>
                                <span>Your wallet </span>
                                <span>{this.state.wallet.substr(0,5) + "..." + this.state.wallet.substr(this.state.wallet.length - 3)}</span>
                            </div>
                            <div>
                                <span>Balance </span>
                                <span>{this.state.balance} LP WBNB-WBT</span>
                            </div>
                            <div className="pt-5  justify-content-between">
                                You are <b className="text-danger">VIP</b>. Please set your telegram ID to use VIP features
                            </div>
                            <div className="pt-5  justify-content-between">
                                <p>Please go to <a href="https://t.me/waterfall_help_bot" rel="noopener noreferrer" target="_blank">https://t.me/waterfall_help_bot</a></p>
                                <p>Say <b>hello</b> to get your telegram ID</p>
                                <p className="pt-3 ">{this.state.telegram ? `Your current telegram ID ${this.state.telegram}` : ''}</p>
                                 <input placeholder={"New telegram ID"} value={this.state.newTelegram} onChange={evt => this.updateInputValue(evt)}/>
                                 <div className="pt-3  justify-content-between">
                                    <button id="btn-bar" onClick={this.updateTelegram.bind(this)}>Update</button>
                                 </div>
                            </div>
                            
                        </div>
                        <VipInfo />
                    </>
                )
            }

        } else {
            return (
                <>
                    <div className="pt-5  justify-content-between">
                        <button id="btn-bar" onClick={this.onConnect.bind(this)}>Connect wallet</button>
                    </div>
                    <VipInfo />
                </>
            )
        }


    }
}
export default VIP
